// Breakpoint設定

$sp: "all and (max-width : 768px)";

$pc: "all and (min-width: 769px)";

// Font-family
@font-face {
    font-family: "Noto Sans CJK JP";
    src: url(../font/NotoSansCJKjp.woff) format('woff'),
         url(../font/NotoSansCJKjp.ttf) format('truetype'),
         url(../font/NotoSansCJKjp.otf) format('opentype');
}
$font-family: 'Noto Sans CJK JP','ヒラギノ角ゴ Pro W3','Hiragino Kaku Gothic Pro','游ゴシック','Yu Gothic','游ゴシック体','YuGothic','ＭＳ Ｐゴシック','MS PGothic',sans-serif;

// カラー

$primary-color: #000;

$secondary-color: #ccc;

$tertiary-color: #eee;


// テキストカラー



// コンテナー設定

$container: 1200px;

// グリッド設定

$gutter: 20px;
$small-gutter: 10px;
