/*
  ----------------
  ホームページ
  ----------------
*/
#loading {
  display: none;
  padding-top: 150px;

  img {
    width: 191px;
    display: block;
    margin: auto;
  }
}

#loader-bg {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: #EFEFEF;
  z-index: 1;
}

#main-contents {
  text-align: center;
}

.main_head {
  padding-top: 42px;
  padding-bottom: 36px;

  img {
    width: 86px;
    display: block;
    margin: auto;
  }
}

.main_img {
  width: 244px;
  height: 244px;
  margin: 0 auto 60px;
  background: url("../img/front_main_back.svg")no-repeat;
  background-size: 244px 244;
  display: flex;
  justify-content: center;
  align-items: center;

  &.toimg {
    background: url("../img/front_main.svg")no-repeat;
  }

  img {
    width: 242px;
    display: block;
    margin: auto;
    z-index: -1;
    display: none;

    &.active {
      display: block;
    }
  }
}

.btn_b_area {
  display: flex;
  justify-content: center;
  margin-bottom: 10px;

  &__item {
    margin: 0 7px;
    width: 72px;
    height: 71px;
    border-radius: 50%;
    background: url("../img/btn_b_back.svg");
    background-size: 72px 71px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    font-size: 18px;
    font-weight: bold;
    color: #929292;

    &.tobtn {
      background: url("../img/btn_b.svg");
    }

    &.active {
      color: #73FDEA;
      background: linear-gradient(to bottom, rgba(112,112,112,0.27), rgba(112,112,112,0.27)),url(../img/btn_b_back.svg);
    }
  }
}

.btn_s_area {
  display: flex;
  justify-content: center;
  padding-bottom: 49px;

  &__item {
    margin: 0 6.5px;
    width: 42px;
    height: 42px;
    border-radius: 50%;
    background: url("../img/btn_s_back.svg");
    background-size: 42px 42px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    font-size: 12px;
    font-weight: bold;
    color: #929292;

    &.tobtn {
      background: url("../img/btn_s.svg");
    }

    &.active {
      color: #73FDEA;
      background: linear-gradient(to bottom, rgba(112,112,112,0.27), rgba(112,112,112,0.27)),url(../img/btn_s_back.svg);
    }
  }
}
